.everythingInHeader{
  font-family: 'Karla', sans-serif;
  width: 95%;
}

.linksInHeader {
  margin-top:20px;
  list-style-type: none;
  display: flex;
  align-items: stretch; /* Default */
  justify-content: space-between;
  width: 95%;
}

.linksInHeader a:link{
  text-decoration: none;
}

.footerStuff{
  font-family: 'Karla', sans-serif;
  padding-top: 10%;
  text-align: center;
  bottom:0;
     z-index:150;
     _position:absolute;
     _top:expression(eval(document.documentElement.scrollTop+
         (document.documentElement.clientHeight-this.offsetHeight)));
     height:35px;
   }

.projectsHeader{
  text-align: center;
}

.projectsContainer{
  width: 50%;
  margin: 0 auto;
}

.listOfProjects{
  display: inline-block;
  text-align: left;
}
.projectTitle{
  text-align: center;
}
.ourListStyles{
  list-style-type: none;
  font-size: 20px;
}

@media (min-width:600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width:800px) {
  body {
    font-size: 30px;
  }
}

